"use client"

import { ProtectedLayout } from "@/components/layout/protected-layout"
import { Card } from "@/components/ui/card"
import { useAuth } from "@/context/auth-context"

export default function DashboardPage() {
  const { user } = useAuth()

  return (
    <ProtectedLayout>
      <div className="p-6 space-y-6 bg-dark min-h-screen">
        <header className="flex items-center justify-between bg-card rounded-xl p-4 shadow-md">
          <h1 className="text-2xl font-semibold text-white">Admin Dashboard</h1>
          <div className="text-gray-300">Welcome, {user?.name}</div>
        </header>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {[
            { title: "Fuel Usage", value: "6,541L" },
            { title: "Distance", value: "31,875 km" },
            { title: "Idle Time", value: "1h 32m" },
            { title: "Revenue", value: "$9,230" },
          ].map((stat) => (
            <Card key={stat.title} className="bg-card p-6 rounded-2xl shadow-lg hover:shadow-xl transition">
              <h2 className="text-gray-400">{stat.title}</h2>
              <p className="text-2xl font-bold text-white mt-2">{stat.value}</p>
            </Card>
          ))}
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <Card className="lg:col-span-2 bg-card p-6 rounded-2xl shadow-lg">
            <h2 className="text-lg font-semibold mb-4 text-gray-100">Fuel Efficiency Overview</h2>
            <div className="h-64 flex items-center justify-center text-gray-400 border border-gray-700 rounded-xl">
              (Chart placeholder)
            </div>
          </Card>

          <Card className="bg-card p-6 rounded-2xl shadow-lg">
            <h2 className="text-lg font-semibold mb-4 text-gray-100">Fleet Details</h2>
            <div className="space-y-3 text-gray-300">
              <p>Truck: <span className="font-medium text-white">ER 14 D 9645</span></p>
              <p>Driver: <span className="font-medium text-white">Kevin Porsian</span></p>
              <button className="w-full bg-accent hover:bg-blue-700 text-white py-2 rounded-lg mt-4">
                Continue
              </button>
            </div>
          </Card>
        </div>
      </div>
    </ProtectedLayout>
  )
}
